p.coords{
    position: fixed;
    left: 10px;
    top: 10px;
}

.Section1{
    display: block;
    height: 250px;
    background: #cccccc;
    padding: 100px 0;
    margin: 100px 0;
    text-align: center;
}

@keyframes section1-left-box-show{
    from{
        margin-left:  0px;
        opacity: 0;
    }

    to{
        margin-left: 200px;
        opacity: 1;
    }
}

@keyframes section1-right-box-show{
    from{
        margin-right:  0px;
        opacity: 0;
    }

    to{
        margin-right: 200px;
        opacity: 1;
    }
}

@keyframes section1-box-hide{
    from{
        opacity: 1;
    }

    to{
        opacity: 0;
    }
}

.Section1 .box{
    width: 250px;
    height: 250px;
    vertical-align: middle;
    font-size: 60px;
    background: #222222;
    color: #DDDDDD;
}

.Section1 .box.left{
    float: left;
    opacity: 0;
    margin-left: 200px;
}

.Section1.animate-show .left{
    animation: section1-left-box-show 0.5s forwards;
}

.Section1 .box.right{
    float: right;
    opacity: 0;
    margin-right: 200px;
}

.Section1.animate-show .right{
    animation: section1-right-box-show 0.5s forwards;
}

.Section1.animate-hide .box
{
    animation: section1-box-hide 0.5s forwards;
}
